import axios from 'axios'
import { Notification } from 'element-ui'

// åˆ¤æ–­æ˜¯å¦ä¸ºChromeæ’ä»¶ç¯å¢ƒ
const isChromeExtension = typeof chrome !== 'undefined' && chrome.runtime && chrome.runtime.id

// æ ¹æ®ç¯å¢ƒè®¾ç½®ä¸åŒçš„baseURL
const baseURL = isChromeExtension ? 'http://xyq.lingtong.xyz/cbg/api/v1' : '/api/v1'

// åˆ›å»ºaxioså®ä¾‹
const request = axios.create({
  baseURL: baseURL, // APIåŸºç¡€è·¯å¾„
  headers: {
    'Content-Type': 'application/json'
  }
})

// è¯·æ±‚æ‹¦æˆªå™¨
request.interceptors.request.use(
  config => {
    // åœ¨å‘é€è¯·æ±‚ä¹‹å‰åšäº›ä»€ä¹ˆ
    console.log('å‘é€è¯·æ±‚:', config.method?.toUpperCase(), config.url)
    return config
  },
  error => {
    // å¯¹è¯·æ±‚é”™è¯¯åšäº›ä»€ä¹ˆ
    console.error('è¯·æ±‚é”™è¯¯:', error)
    return Promise.reject(error)
  }
)

// å“åº”æ‹¦æˆªå™¨
request.interceptors.response.use(
  response => {
    // å¯¹å“åº”æ•°æ®åšäº›ä»€ä¹ˆ
    const { data } = response
    
    return data
  },
  error => {
    // å¯¹å“åº”é”™è¯¯åšäº›ä»€ä¹ˆ
    console.error('å“åº”é”™è¯¯:', error)
    
    let message = 'è¯·æ±‚å¤±è´¥'
    let responseData = null
    
    if (error.response) {
      // æœåŠ¡å™¨å“åº”äº†é”™è¯¯çŠ¶æ€ç 
      const { status, data } = error.response
      message = data.message || `è¯·æ±‚å¤±è´¥ (${status})`
      
      // å¦‚æœåç«¯è¿”å›äº†dataå­—æ®µï¼Œä½¿ç”¨åç«¯çš„dataï¼›å¦åˆ™ä½¿ç”¨åç«¯çš„å®Œæ•´å“åº”
      if (data.data !== undefined) {
        responseData = data.data
      } else {
        responseData = data
      }
      
      console.log('é”™è¯¯å“åº”æ•°æ®:', responseData)
    } else if (error.request) {
      // è¯·æ±‚å·²å‘å‡ºä½†æ²¡æœ‰æ”¶åˆ°å“åº”
      message = 'ç½‘ç»œé”™è¯¯ï¼Œè¯·æ£€æŸ¥ç½‘ç»œè¿æ¥'
    } else {
      // å…¶ä»–é”™è¯¯
      message = error.message || 'è¯·æ±‚å¤±è´¥'
    }
    
    Notification.error({
      title: 'é”™è¯¯',
      message: message
    })
    
    return {
      code: error.response?.status || 500,
      data: responseData,  // ä½¿ç”¨åç«¯è¿”å›çš„dataï¼Œè€Œä¸æ˜¯å¼ºåˆ¶è®¾ç½®ä¸ºnull
      message: message,
      timestamp: Date.now()
    }
  }
)

// å¯¼å‡ºå°è£…çš„è¯·æ±‚æ–¹æ³•
export const api = {
  // GETè¯·æ±‚
  get(url, params = {}) {
    return request.get(url, { params })
  },
  
  // POSTè¯·æ±‚
  post(url, data = {}) {
    return request.post(url, data)
  },
  
  // PUTè¯·æ±‚
  put(url, data = {}) {
    return request.put(url, data)
  },
  
  // DELETEè¯·æ±‚
  delete(url, params = {}) {
    return request.delete(url, { params })
  },
  
  // ä¸‹è½½æ–‡ä»¶
  download(url, params = {}, filename) {
    return request.get(url, { 
      params,
      responseType: 'blob'
    }).then(response => {
      if (response.code === 200) {
        // åˆ›å»ºä¸‹è½½é“¾æ¥
        const blob = new Blob([response.data])
        const downloadUrl = window.URL.createObjectURL(blob)
        const link = document.createElement('a')
        link.href = downloadUrl
        link.download = filename || 'download'
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
        window.URL.revokeObjectURL(downloadUrl)
      }
      return response
    })
  }
}

export default request 